<template>
    <!-- 过渡动画 -->
    <transition name="fade">
        <div class="img" @click="bigImg">
            <img :src="imgSrc">
        </div>
    </transition>
</template>
<script>
export default {
  props: ['imgSrc'],
  methods: {
    bigImg () {
      // 发送事件
      this.$emit('clickit')
    }
  }
}
</script>
<style scoped>
/*动画*/
.fade-enter-active,
.fade-leave-active {
    transition: all .2s linear;
    transform: translate3D(0, 0, 0);
}

.fade-enter,
.fade-leave-active {
    transform: translate3D(100%, 0, 0);
}


/*不限制图片大小，实现居中*/
img {
    max-width: 100%;
    display: block;
    position: absolute;
    left: 0;
    right: 0;
    margin: auto;
    z-index: 1000;
}
</style>



