import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/page/index'
import Content from '@/page/content'
import ImageViewer from '@/page/imageViewer'
import Rule from '@/page/rule'
import Manual from '@/page/manual'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      component: Index
    }, {
      path: '/content/:id',
      component: Content
    }, {
      path: '/image/:module/:name',
      component: ImageViewer
    }, {
      path: '/rule/',
      component: Rule
    }, {
      path: '/manual/',
      component: Manual
    }
  ]
})



