              </router-link>
              <span>2017-3-15</span>
            </div>
            <div class="rulelist">
              <router-link to="/image/rule/r-wljjxz">
              <span>钜商网采购招标服务平台网络竞价须知</span>
              </router-link>
              <span>2017-3-28</span>
            </div>
            <div class="rulelist">
              <router-link to="/image/rule/r-zrfzzcl">
              <span>钜商网转让方资质材料</span>
              </router-link>
              <span>2017-3-28</span>
            </div>
            <div class="rulelist">
