              <span>交易合同（模板-供交易双方参考）</span>
              </router-link>
              <span>2017-3-29</span>
            </div>
    </div>
    <hr>
    <myFooter></myFooter>
  </div>
</template>
<script>
import myHeader from '../components/myheader.vue'
import myFooter from '../components/myfooter.vue'
export default {
  components: { myHeader, myFooter }
}
</script>
<style>
  .rulelist-container {
    height: auto;
    margin-bottom: 20px;
  }
  .rulelist {
    font-size:50%;
    position: relative;
    height: auto;
    padding-top: 20px;
    background: #fff;
    box-shadow: inset 0px -1px 1px -1px #A7A7AB;
  }
</style>



