<template>
  <div>
    <h4 v-text="dat.Title" style="text-align:center;"></h4>
    <p style="font-size:50%;text-align:center;">来源：{{dat.From}}　　发布于:{{dat.PublishDate}}</p>
    <hr>
    <article v-html="dat.Content" style="font-size:50%;text-align:center;"></article>
    <hr>
    <myFooter></myFooter>
  </div>
</template>
<script>
import myHeader from '../components/myheader.vue'
import myFooter from '../components/myfooter.vue'
export default {
  components: { myHeader, myFooter },
  data () {
    return {
      id: this.$route.params.id,
      dat: {}
    }
  },
  created () {
    this.getData()
  },
  methods: {
    getData () {
      this.$api.get('?service=Contents.GetContentsDetail&detail_id=' + this.id, null, r => {
        this.dat = r.data.info[0]
      })
    }
  }
}
</script>



