<template>
    <div>
        <big-img v-if="showImg" @clickit="viewImg" :imgSrc="imgSrc"></big-img>
    </div>
</template>

<script>
import BigImg from '../components/BigImg.vue'
export default {
  data () {
    return {
      showImg: true,
      imgSrc: '../../static/image/' + this.$route.params.name + '.png'
    }
  },
  components: {
    'big-img': BigImg
  },
  methods: {
    viewImg () {
      this.showImg = false
      this.$router.push('/' + this.$route.params.module)
    }
  }
}
</script>



