<template>
  <div>
  	<myHeader></myHeader>
    <hr>
    <div class="newslist-container">
        <div style="font-size:50%;" class="newslist" v-for="item in list">
            <div>
              <router-link :to="'/content/' + item.id">
              <span>{{item.Title}}</span>
              </router-link>
              <span>{{item.PublishDate}}</span>
            </div>
        </div>
    </div>
    <hr>
    <myFooter></myFooter>
  </div>
</template>
<script>
import myHeader from '../components/myheader.vue'
import myFooter from '../components/myfooter.vue'
export default {
  components: { myHeader, myFooter },
  data () {
    return {
      list: []
    }
  },
  created () {
    this.getData()
  },
  methods: {
    getData () {
      this.$api.get('?service=Contents.GetContentsList', null, r => {
        this.list = r.data.info
      })
    }
  }
}
</script>
<style>
  .newslist-container {
    height: auto;
    margin-bottom: 20px;
  }
  .newslist {
    position: relative;
    height: auto;
    padding-top: 20px;
    background: #fff;
    box-shadow: inset 0px -1px 1px -1px #A7A7AB;
  }
</style>



